.contact-container {
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin: 5rem 0rem;
  /* width: 100%; */
  /* align-items: center; */
}

.contact-left {
  width: 50%;
  padding: 20px;
}

.contact-right {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.icon {
  font-size: 24px;
}

.contact-item {
  width: 42%;
}

.contact-box {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 25px 23px 0px -6px rgba(0, 0, 0, 0.1),
    0px 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.contact-box:hover {
  animation: shake 0.5s infinite ease-in-out;
}
@keyframes shake {
  0% {
    transform: rotate(-2deg);
  }
  50% {
    transform: rotate(2deg);
  }
  100% {
    transform: rotate(-2deg);
  }
}
.big-icon {
  font-size: 48px;
  margin-bottom: 10px;
}
.contact-box p {
  font-size: 1rem;
}
@media (max-width: 1000px) {
  .contact-box p {
    font-size: 0.8rem;
  }
}
@media (max-width: 837px) {
  .contact-container {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  .contact-box p {
    font-size: 1rem;
  }
  .contact-right {
    width: 90%;
  }
}
@media (max-width: 680px) {
  .contact-box p {
    font-size: 0.9rem;
  }
  .contact-container {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .contact-right {
    width: 90%;
  }
}
@media (max-width: 500px) {
  .contact-box p {
    font-size: 1rem;
  }
  .contact-right {
    flex-direction: column;
    gap: 30px;
  }
  .contact-item {
    width: 60%;
  }
}
