.collage {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.collage h2 {
  font-size: 2rem;
}

/* Style for the container div of the collage */
.collage div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow: hidden;
  margin-top: 2rem;
}

/* Style for individual image div */
.collage div .imgs {
  margin: 0.3rem;
  width: 20rem;
  height: 20rem;
  overflow: hidden;
}

/* Style for images in the collage */
.collage div .imgs img {
  width: 100%; /* Fill the entire container */
  height: 100%; /* Fill the entire container */
  transition: all 1.1s ease-in-out;
  /* filter: grayscale(100%); */
}

/* Scale effect on hover */
.collage div .imgs:hover img {
  /* filter: grayscale(0%); */
  transform: scale(1.1);
}
