.main-intro {
  max-width: 1500px;
  height: 60vh;
  margin: 40px auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  /*  */
  flex-wrap: wrap;
  gap: 20px;
  /*  */
  /* height: 80vh; */
}

.image-container {
  width: 280px;
  height: 280px;
  border-radius: 50%;
  overflow: hidden;
}
/* .image-container {
  width: 18%;
  height: 50%;
  border-radius: 50%;
  overflow: hidden;
} */
/* .image-container {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    overflow: hidden;
  } */
.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.intro-text {
  margin-left: 40px;
  text-align: center;
  font-size: 1.5rem;
}

/* .intro-text {
  margin-left: 40px;
  text-align: center;
} */
.intro-text p {
  margin: 0;
  line-height: 1.5;
}
.intoTextName {
  font-weight: bold;
  font-size: 2rem;
}
.intro-text a {
  color: #405b78; /* Change the color as needed */
  text-decoration: underline;
}

.intro-text a:hover {
  text-decoration: underline;
}
@media (max-width: 580px) {
  .main-intro {
    /* flex-direction: column; */
    flex-wrap: wrap;
  }
  .image-container {
    width: 200px;
    height: 200px;
  }
  .intro-text {
    /* margin-left: 40px; */
    margin: 0;
    font-size: 1.2rem;
  }
  .intoTextName {
    font-size: 1.8rem;
  }
}
@media (max-width: 460px) {
  .main-intro {
    /* flex-direction: column; */
    flex-wrap: wrap;
  }
  .image-container {
    width: 200px;
    height: 200px;
  }
  .intro-text {
    /* margin-left: 40px; */
    margin: 0;
    font-size: 1.3rem;
  }
  .intoTextName {
    font-size: 1.5rem;
  }
}
.icon {
  color: black;
}

.icon:hover {
  color: #333; /* Change the color on hover if needed */
}
