.mainAbout {
  /* height: 0vh; */
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 70px; /* Adjust as needed */
}

.about-image {
  /* flex: 0 0 40%; */
  width: 250px;
  height: 250px;
  border-radius: 50%;
  overflow: hidden;
}

.about-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-text {
  flex: 0 0 50%;
  text-align: center;
  width: 80%;
}

.about-text h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.about-text p {
  font-size: 1.2rem;
  line-height: 1.6;
}
@media (max-width: 545px) {
  .mainAbout {
    flex-wrap: wrap-reverse;
    justify-content: center;
  }
  .about-text {
    flex: 0 0 90%;
    width: 90%;
  }
}
