.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  color: black;
  position: relative;
}

.navbar-brand {
  font-size: 1.75rem;
  font-weight: bold;
}
.navbar-brand a {
  font-size: 1.75rem;
  font-weight: bold;
  color: black;
  text-decoration: none;
}

.navbar-links {
  display: flex;
  flex-direction: row;
}

.navbar-link {
  padding: 5px;
  color: black;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s ease;
}

.navbar-link:hover {
  /* color: #cbd5e0; */
}

.hamburger-menu {
  display: none;
  border: none;
  background: none;
  font-size: 1.5rem;
  cursor: pointer;
}

@media (max-width: 500px) {
  .navbar-links {
    display: none;
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    background-color: rgba(221, 231, 235, 0.8);
    padding: 1rem;
    z-index: 1000;
    transition: top 2s ease-in-out; /* Add transition effect */
  }
  .hamburger-menu {
    display: block;
  }
  .navbar-links.show {
    display: flex;
    top: 100%;
    justify-content: space-evenly;
    align-items: center;
  }
}
