.technology {
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-right: 20px; /* Adjust margin between Technology components */
}

.logo-container {
  width: 150px; /* Adjust width of the container as needed */
  height: 150px; /* Adjust height of the container as needed */
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.technology-logo {
  max-width: 80%; /* Adjust size of the logo */
  max-height: 80%; /* Adjust size of the logo */
}

.technology-name {
  margin-top: 5px; /* Adjust margin between logo and name */
  font-size: 14px; /* Adjust font size of the name */
}
