.product-card {
    position: relative;
    overflow: hidden;
    width: 200px; /* Adjust width as needed */
    border: 1px solid #ccc; /* Add border for card effect */
    border-radius: 8px; /* Add border radius for rounded corners */
  }
  
  .product-image {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black overlay */
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .product-card:hover .overlay {
    opacity: 1;
  }
  
  .overlay-content {
    text-align: center;
  }
  
  .product-name {
    color: white;
    font-size: 1.25rem; /* Adjust font size as needed */
    font-weight: bold;
    margin-bottom: 0.5rem; /* Adjust spacing as needed */
  }
  
  .product-description {
    color: white;
    font-size: 0.875rem; /* Adjust font size as needed */
    margin-bottom: 0.5rem; /* Adjust spacing as needed */
  }
  
  .product-link {
    color: #3182ce; /* Adjust link color as needed */
    text-decoration: underline;
  }
  