.project-card {
  max-width: 25rem; /* 448px */
  width: 28%;
  width: 25rem;
  height: 30rem;
  /* margin-left: auto;
  margin-right: auto; */
  background-color: #ffffff;
  border-radius: 0.75rem; /* 12px */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
}

.project-image {
  margin: 10px 0;
  width: 100%;
  height: 12rem;
  object-fit: contain;
  object-position: center;
}

.project-details {
  padding: 2rem; /* 32px */
  text-align: center;
}

.project-title {
  text-transform: uppercase;
  letter-spacing: 0.05em; /* 0.8px */
  color: #667eea;
  font-weight: 600;
  font-size: 0.875rem; /* 14px */
}

.project-name {
  display: block;
  margin-top: 0.25rem; /* 4px */
  font-size: 1.125rem; /* 18px */
  line-height: 1.25;
  color: #000000;
}

.project-description {
  margin-top: 0.5rem; /* 8px */
  color: #6b7280;
}

.project-technologies {
  margin-top: 1rem; /* 16px */
}

.technology-tag {
  display: inline-block;
  background-color: #e5e7eb;
  border-radius: 9999px;
  padding: 0.25rem 0.75rem; /* 4px 12px */
  font-weight: 600;
  font-size: 0.875rem; /* 14px */
  color: #4b5563;
  margin: 0.5rem 0 0;
  margin-right: 0.5rem; /* 8px */
}

.project-link {
  color: #3182ce; /* Adjust link color as needed */
  text-decoration: underline;
}
